<template>
	<v-row>
		<v-col>
			<v-sheet
				min-height="70vh"
				rounded="lg"
			>
				<h2 class="page-title">
					<span v-if="this.$route.name != '예약하기'"><strong class="display-1 blue--text">{{ nameInfo }}</strong>님의</span> {{ this.$route.name }}
				</h2>
				
				<v-form
					ref="form"
					v-model="valid"
					lazy-validation
				>
					<v-select
						label="객실"
						v-model="roomInfo"
						:items="roomSelectItems"
						:readonly="editMode <= 0"
						required
						:rules="roomRules"
					></v-select>
					
					<v-text-field
						v-model="dateRangeText"
						label="입실일&퇴실일"
						readonly
						v-if="!editMode"
					></v-text-field>
					
					<v-dialog
						ref="dialog"
						v-model="modal"
						persistent
						width="290px"
						v-if="editMode"
					>
						<template v-slot:activator="{ on, attrs }">
							<v-text-field
								v-model="dateRangeText"
								label="입실일&퇴실일"
								v-bind="attrs"
								v-on="on"
								required
								:rules="dateRules"
							></v-text-field>
						</template>
						<v-date-picker
							v-model="datesInfo"
							range
							disable
						>
							<v-spacer></v-spacer>
							<v-btn
								text
								color="primary"
								@click="modal = false"
							>
								Cancel
							</v-btn>
							<v-btn
								text
								color="primary"
								@click="$refs.dialog.save(datesInfo)"
							>
								OK
							</v-btn>
						</v-date-picker>
					</v-dialog>
					
					<v-text-field
						label="예약자명"
						v-model="nameInfo"
						:readonly="editMode < 1"
						required
						:rules="nameRules"
					></v-text-field>

					<v-text-field
						label="연락처"
						v-model="phoneInfo"
						:readonly="editMode < 1"
						required
						:rules="telRules"
					></v-text-field>

					<v-row>
						<v-col
							cols="12"
							sm="6"
						>
							<v-select
								label="추가인원"
								v-model="addInfo"
								:items="countSelectItems"
								:readonly="editMode < 1"
								required
								:rules="addRules"
							></v-select>
						</v-col>
						<v-col
							cols="12"
							sm="6"
						>
							<v-select
								label="수영장 온수"
								v-model="poolInfo"
								:items="poolSelectItems"
								:readonly="editMode < 1"
								required
								:rules="poolRules" 
							></v-select>
						</v-col>
					</v-row>

					<v-text-field
            v-model="passwordInfo"
            :rules="passwordRule"
            :type="'password'"
            label="비밀번호"
						v-if="this.$route.name !='예약상세'"
          ></v-text-field>

				</v-form>
				
				<v-row
					justify="center"
					class="py-9"
					v-if="this.$route.name == '예약상세'"
  			>
					<v-btn
						depressed
						large
						class="mx-1 white--text"
						color="teal"
						:to="{name:'예약수정', query:{id:id+1}}"
						
					>
						<v-icon left>
							mdi-pencil
						</v-icon>
						예약 내역 수정</v-btn>
					
					<v-btn
						depressed
						large
						to="/reserve"
						class="mx-1 white--text"
						color="blue-grey"
					>
						목록</v-btn>
				</v-row>

				<v-row
					justify="center"
					class="py-9"
					v-else
  			>
					
					<v-btn
						depressed
						large
						class="mx-1"
						color="primary"
						@click="validate"
					>
						<v-icon left>
							mdi-pencil
						</v-icon>
						확인</v-btn>
					<v-btn
						depressed
						large
						to="/reserve"
						class="mx-1 white--text"
						color="blue-grey"
					>
						취소</v-btn>	
				</v-row>

				<v-snackbar
					v-model="snackbar"
					:timeout="1000"
				>
					비밀번호를 다시 확인해주세요

				</v-snackbar>
				
			</v-sheet>
		</v-col>
	</v-row>
</template>

<script>
import data from '@/assets/data/reserve.js';
export default {
  name: 'reservedetail',
	components: {
		
	},
	data () {
		return {
			//items:{예약자명:""},
			id:"",
			valid: true,
			editMode:1,
			modal: false,
			snackbar: false,
			
			datesInfo:[],
			nameInfo:"",
			roomInfo:"",
			addInfo:"",
			phoneInfo:"",
			poolInfo:"",
			passwordInfo:"",
			countSelectItems:['1명', '2명', '3명'],
			roomSelectItems :['ROOM 하나', 'ROOM 둘', 'ROOM 셋'],
			poolSelectItems:['예', '아니오'],
			
			roomRules: [
        v => !!v || '객실을 선택해주세요'
      ],
			dateRules: [
        v => !!v || '입퇴실일을 선택해주세요'
      ],
			nameRules: [
        v => !!v || '예약자명을 입력해주세요'
      ],
			telRules: [
        v => !!v || '연락처를 입력해주세요'
      ],
			addRules: [
        v => !!v || '추가인원을 선택해주세요'
      ],
			poolRules: [
        v => !!v || '수영장 온수를 선택해주세요'
      ],
			passwordRule:[
				v => !!v || '비밀번호를 입력해주세요.',
				v => {
					const replaceV = v.replace(/(\s*)/g, '')
					return replaceV.length >= 4 || '4자리 이상 입력해주세요.'
				}
			]
		}
	},
	methods: {
		validate () {
			console.log(this.$route.name)
			if (this.$refs.form.validate()) {
        //console.log(this.roomInfo, this.datesInfo, this.nameInfo, this.phoneInfo, this.addInfo, this.poolInfo, this.passwordInfo);
				if(this.passwordInfo != this.items.비밀번호) this.snackbar=true;
				else{
					
				}
      }
		},
		init(){
			this.id = this.$route.query.id-1;
			this.editMode = 1;
			//this.roomInfo=this.roomSelectItems[this.id];

			if(this.$route.name != "예약하기"){
				if(this.$route.name =="예약상세") this.editMode = 0;
				this.items = data[this.id];
				this.roomInfo = this.items.객실;
				this.datesInfo = this.items.입퇴실일;
				this.nameInfo = this.items.예약자명;
				this.phoneInfo = this.items.연락처;
				this.addInfo = this.items.추가인원;
				this.poolInfo =  this.items.수영장온수;
				//this.passwordInfo =  this.items.비밀번호;
			}
		}
	},
	watch:{
		'$route.path': function(val, oldVal){
			this.init()
		}
	},
	mounted() {
		this.init()
	},
	computed: {
		dateRangeText () {
			return this.datesInfo.join(' ~ ')
		},
	},
}
</script>

